import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import "../styles/main.scss";
import history from "./history";
import { HashRouter, Route, Switch } from "react-router-dom";
import LoginPage from "../views/login_page";
import PortalPage from "../views/portal_page";
import PrivateRoute from "./private-route";
import { AuthProvider } from "./auth-route";
import DisplayPage from "../views/portal_page_display";
import EditPage from "../views/portal_edit_page";
function App() {
  return (
    <AuthProvider>
      <HashRouter basename="/OD1N-Health/">
        <Switch>
          <Route
            path={["/", "/index", "/login", "/signin"]}
            exact
            component={LoginPage}
          />
          <PrivateRoute path="/portal" exact component={PortalPage} />
          <PrivateRoute path="/portal/display" exact component={DisplayPage} />
          <PrivateRoute path="/portal/edit" exact component={EditPage} />
        </Switch>
      </HashRouter>
    </AuthProvider>
  );
}

export default App;
