import React, { useEffect, useState } from "react";
import { Container, Form, Button } from "react-bootstrap";
import { getDatabase, ref, onValue, update } from "firebase/database";
import history from "../scripts/history";

const EditPage = () => {
  const [selected, setSelected] = useState("Hydration");
  const [level, setLevel] = useState(1);

  const Options = () => {
    var levels = 0;
    if (selected !== undefined) {
      if (selected === "Hydration") {
        levels = 7;
      } else if (selected === "PH Levels") {
        levels = 7;
      } else if (selected === "Sugar Levels") {
        levels = 6;
      } else if (selected === "Fat Loss") {
        levels = 7;
      }
      const optionsList = [];
      for (let i = 1; i <= levels; i++) {
        optionsList.push(<option value={i}>{i}</option>);
      }
      return (
        <Form.Select
          id="level_select"
          onChange={() => {
            setLevel(document.getElementById("level_select").value);
          }}>
          {optionsList}
        </Form.Select>
      );
    }
    return null;
  };

  const database = getDatabase();
  const updateRecos = async () => {
    const reco = document.getElementById("reco").value;
    const updates = {};
    updates[`/recommendations/${selected}/${level}/0`] = reco;
    update(ref(database), updates)
      .then(() => {
        alert("Recommendation has successfully been added!");
        return;
      })
      .catch((err) => {
        alert(err);
      });
  };

  return (
    <div>
      <Container>
        <h1 className="mt-5 text-center">Edit Recommendations</h1>
        <Button
          className="w-100 mb-3 mt-2"
          variant="outline-dark"
          onClick={(e) => {
            e.preventDefault();
            history.push("/OD1N-Health/#/portal/");
            window.location.reload();
          }}>
          Back to Main
        </Button>
        <Form
          onSubmit={(e) => {
            e.preventDefault();
            updateRecos();
          }}>
          <Form.Label>Select Category</Form.Label>
          <Form.Select
            id="category_select"
            onChange={() => {
              setSelected(document.getElementById("category_select").value);
            }}>
            <option value="Hydration">Hydration</option>
            <option value="Liver Health">Liver Health</option>
            <option value="Kidney Health">Kidney Health</option>
            <option value="UTI Infection">UTI Infection</option>
            <option value="Sugar Levels">Sugar Levels</option>
            <option value="Fat Loss">Fat Loss</option>
            <option value="PH Levels">PH Levels</option>
          </Form.Select>
          <Form.Label className="mt-3">Select Level</Form.Label>
          <Options />

          <Form.Label className="mt-3">Recommendation</Form.Label>
          <Form.Control as="textarea" rows={3} required id="reco" />

          <Button variant="outline-dark" type="submit" className="mt-3">
            Submit
          </Button>
        </Form>
      </Container>
    </div>
  );
};

export default EditPage;
