import React, { useEffect, useState } from "react";
import { Container, Form, Button, Table } from "react-bootstrap";
import { getDatabase, ref, onValue, update } from "firebase/database";
import history from "../scripts/history";

const DisplayPage = () => {
  const [selected, setSelected] = useState("Hydration");
  const database = getDatabase();

  const Datarows = () => {
    const tableRows = [];
    const newRef = ref(database, `/recommendations/${selected}`);
    onValue(newRef, (snapshot) => {
      const data = snapshot.val();
      console.log(data);
      for (let i = 0; i < data.length; i++) {
        tableRows.push(
          <tr key={i}>
            <td>{Object.keys(data)[i]}</td>
            <td>{Object.values(data)[i]}</td>
          </tr>
        );
      }
    });
    return (
      <Table striped bordered hover>
        <thead>
          <tr>
            <th>Level</th>
            <th>Recommendation</th>
          </tr>
        </thead>
        <tbody>{tableRows}</tbody>
      </Table>
    );
  };

  return (
    <div>
      <Container>
        <h1 className="mt-5 text-center">Display Recommendations</h1>
        <Button
          className="w-100 mb-3 mt-2"
          variant="outline-dark"
          onClick={(e) => {
            e.preventDefault();
            history.push("/OD1N-Health/#/portal/");
            window.location.reload();
          }}>
          Back to Main
        </Button>
        <Form.Label>Select Category</Form.Label>
        <Form.Select
          id="category_select"
          className="mb-4"
          onChange={() => {
            setSelected(document.getElementById("category_select").value);
          }}>
          <option value="Hydration">Hydration</option>
          <option value="Liver Health">Liver Health</option>
          <option value="Kidney Health">Kidney Health</option>
          <option value="UTI Infection">UTI Infection</option>
          <option value="Sugar Levels">Sugar Levels</option>
          <option value="Fat Loss">Fat Loss</option>
          <option value="PH Levels">PH Levels</option>
        </Form.Select>
        <Datarows />
      </Container>
    </div>
  );
};
export default DisplayPage;
